<template>
	<div id="statcenter-current">
		<public-header title="详情"></public-header>
		<div class="content padding-15">
			<van-row class="c-gray flex-lr font-15">
				<van-col span="6" class="padding-10-tb">账期</van-col>
				<van-col span="5" class="padding-10-tb">已完成 <br>单数</van-col>
				<van-col span="5" class="padding-10-tb">总营业额</van-col>
				<van-col span="4" class="padding-10-tb">总入账</van-col>
				<van-col span="4" class="padding-10-tb">总退款</van-col>
			</van-row>
			<van-row>
				<van-col span="6">总计</van-col>
				<van-col span="5">{{stat.total_success_order}}</van-col>
				<van-col span="5">{{stat.total_fee}}</van-col>
				<van-col span="4">{{stat.total_final_fee}}</van-col>
				<van-col span="4">{{stat.total_refund_fee}}</van-col>
			</van-row>
			<van-row v-for="(item, index) in detail" :key="index" v-if="detail">
				<van-col span="6">{{item.stat_day}}</van-col>
				<van-col span="5">{{item.total_success_order}}</van-col>
				<van-col span="5">{{item.total_fee}}</van-col>
				<van-col span="4">{{item.final_fee}}</van-col>
				<van-col span="4">{{item.refund_fee}}</van-col>
			</van-row>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {mapState} from 'vuex'
	import publicHeader from "@/components/header"
	import {getData} from "../../controller/funcCommon.js"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				stat: {},
				detail: []
			}
		},
		methods: {
			onLoad() {
				let that = this;
				getData({
					vue: this,
					data: {
						detail: 1,
						filter: that.filter ? that.filter.items: {}
					},
					url: 'plateform/statcenter/takeout/takeout',
					success: function(result) {
						that.detail = result.detail;
						that.stat = result.stat;
					}
				});
			}
		},
		computed: {
			...mapState([
				'filter',
			]),
		},
		mounted() {
			this.onLoad();
		},

	}
</script>

<style scoped>
	#statcenter-current .van-row{
		font-size: 14px;
		text-align: center;
	}
	#statcenter-current .van-row:nth-child(odd) {
		background-color: #ddd;
	}
	#statcenter-current .van-row .van-col{
		padding: 8px 0;
		border-right: 1px solid #eee;
	}
	#statcenter-current .van-row .van-col:last-child{
		border-right: 0px;
	}
</style>


